<template>
	<view class="container">
		<view>
			<view class="example-body">
				<uni-nav-bar :fixed="true" color="#333333" background-color="#FFFFFF" @clickLeft="handle_plat" @clickRight="handleSearchOpen">
					<block slot="left"><text class="iconfont iconguanbi f18"></text></block>
					<view class="input-view f18 uni_3030">平城热线</view>
					<block slot="right"><text class="iconfont iconsousuo f22"></text></block>
				</uni-nav-bar>
			</view>
		</view>
		<view>
			<view class="b-l" >
				 <image  v-if="platform != 'ios'" class="index_banner" src="../../static/images/001.png" @click="handleIphone"></image> 
				<a v-if="platform == 'ios'" class="ico_phone_box" :href="'tel:' + phone">
					<image class="index_banner" src="../../static/images/001.png" @click="handleIphone"></image>
				</a>
			</view>
			<view class="index_list">
				<!--S 诉求列表 -->
				<view v-if="list.length > 0">
					<view v-for="(item, index) in list" :key="index" class="uni-divider-line mb15">
						<view @click="handleDetail(item)">
							<view class="uni-flex-list mb15">
								<text class="list_name ">{{ handler_hide(item.submitterName) }}</text>
								<view class="list_num">
									<text>诉求编号：</text>
									<text>{{ item.networkId }}</text>
								</view>
							</view>
							<view class="list_name text_ellipsis_two font-weigth-none mb15">{{ item.networkContent }}</view>
							<view class="uni-flex-list_img">
								<view class="img" v-for="(itemson, index) in item.networkFile">
									<image :src="img_url + itemson" alt="错误" class="uni_width uni_height" @error="handle_image_error(itemson, index)" mode="aspectFill"></image>
								</view>
							</view>
						</view>
						<view class="uni-flex-list mb15">
							<text class="list_time">{{ item.submitTime }}</text>
							<!-- S@click="handleFollow(item)"  S1 明确需求  -->
							<!-- <view @click="handleFollow(item)">
								<image v-if="item.isFollow == '1'" src="../../static/images/heart_default.png" mode="" class="list_follow uni-vertical-align"></image>
								<image v-if="item.isFollow == '2'" src="../../static/images/heart.png" mode="" class="list_follow uni-vertical-align"></image>
								<text class="pl8">{{ item.isFollowDesc }}</text>
							</view> -->
						</view>
						<!--E  诉求列表 -->
					</view>
					<view class="uni-loadmore" v-if="showLoadMore">{{ loadMoreText }}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	components: {},
	data() {
		return {
			img_url: this.$config.imgPath,
			title: '平城热线',
			params: {
				page: 1,
				limit: 10,
				userId: ''
			},
			reload: false,
			list: [], //列表数据
			imageList: [], //图片数组
			loadMoreText: '加载中...',
			showLoadMore: false,
			loadingType: 0,
			platform:'',
			phone:'0352-5808890'
		};
	},
	onTabItemTap(object) {
	},
	onLaunch() {
		uni.stopPullDownRefresh();
	},

	onLoad(data) {
		if (uni.getSystemInfoSync().platform == 'ios') {
			var submitterName = this.decode(data.submitterName);
			uni.setStorageSync('submitterName', submitterName);
		} else {
			uni.setStorageSync('submitterName', data.submitterName);
		}
		uni.setStorageSync('submitterId', data.submitterId);
		uni.setStorageSync('submitterPhone', data.submitterPhone);
		this.params.userId = data.submitterId;
		this.getSystemInfo()
	},
	onShow() {
		uni.removeStorageSync('keyword')
		this.list=[];
		this.params.page=1
		this.loadData();
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = 'fixed';
		} else {
			this.headerPosition = 'absolute';
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.reload = true;
		this.params.page= 1;
		this.list=[]
		this.loadData();
	},
	//加载更多
	onReachBottom() {
		//阻止重复加载
		if (!this.showLoadMore) {
			this.showLoadMore = true;
			this.params.page += 1;
			this.loadData();
		}
	},
	/*S 导航 搜索按钮*/
	/*E 导航 搜索按钮*/
	methods: {
		/*S 打电话*/
		getSystemInfo(){
			var _this=this;
			uni.getSystemInfo({
				success: (res) => {
					_this.platform = res.platform
				}
			})
		},
		handleIphone(tel){
			uni.makePhoneCall({
			    phoneNumber: tel + this.phone
			});
		},
		/*E 打电话*/
		decode(str) {
			return decodeURIComponent(
				atob(str)
					.split('')
					.map(function(c) {
						return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
					})
					.join('')
			);
		},
		/*S 关闭按钮*/
		handleBack() {
			// window.location = 'flutter//goBack'
			if (uni.getSystemInfoSync().platform == 'ios') {
				window.location = 'ios://details';
			} else {
				window.android.finish();
			}
		},
		handle_plat() {
			var _this = this;
			uni.showModal({
				title: '温馨提示',
				content: '是否关闭该应用',
				success: function(res) {
					if (res.confirm) {
						_this.handleBack();
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		/*e 关闭按钮*/
		/*S 初始数据*/
		loadData() {
			this.loadingType = 0;
			this.imageList = [];
			var _this = this;
			_this.$minApi.networkWorkOrderPage({ ..._this.params }).then(res => {
				if (res.code == '0') {
					if (res.data.result.length < _this.params.limit) {
						_this.showLoadMore = true;
						_this.loadMoreText = '没有更多数据了';
					} else {
						_this.showLoadMore = false;
					}
					res.data.result.map(i => {
						if (!!i.networkFile) {
							i.networkFile = JSON.parse(i.networkFile);
						}
					});
					_this.imageList = [];
					_this.list = _this.list.concat(res.data.result);
					_this.list.map(i => {
						_this.imageList.push(i.networkFile);
					});
				} else {
					_this.list = [];
					_this.showLoadMore = false;
					if (_this.params.page > 1) {
						_this.params.page -= 1;
					}
					uni.showToast({
						title: '暂无数据',
						duration: 3000
					});
				}
			});
			uni.stopPullDownRefresh();
		},

		/*E 初始数据*/
		/*S 导航 搜索按钮 */
		handleSearchOpen() {
			uni.navigateTo({
				url: '/pages/searchAttention/index'
			});
		},
		/*E导航 搜索按钮 */
		/*S 详情 */
		handleDetail(item) {
			let id = item.networkId;
			let submitterId = this.params.userId;
			uni.navigateTo({
				url: `/pages/detail?id=${id}&submitterId=${submitterId}`
			});
		},
		/*E 详情 */
		/*S 图片加载失败 */
		handle_image_error: function(item, index) {
			// item = this.imageError
			// this.$set(item, index, this.imageError);
		},
		/*E 图片加载失败 */

		/*S 取消关注 */
		handleFollow(item) {
			var followlist = {
				networkId: item.networkId,
				userId: this.params.userId
			};
			if (item.isFollow == '2') {
				// 未关注
				this.$minApi.networkWorkOrderCancelFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						item.isFollow = '1';
						item.isFollowDesc = '未关注';
						uni.showToast({
							title: '取消关注成功',
							icon: 'none'
						});
						/* this.list = [];
						this.loadData(); */
					}
				});
			} else {
				// 已关注
				this.$minApi.networkWorkOrderFollow({ ...followlist }).then(res => {
					if (res.code == 0) {
						uni.showToast({
							title: '关注成功',
							icon: 'none'
						});
						item.isFollow = '2';
						item.isFollowDesc = '已关注';
						/* this.list = [];
						this.loadData(); */
					}
				});
			}
		},
		/*E 取消关注*/
		/*S 隐藏字符*/
		handler_hide(string) {
			if (string) {
				let arrBox = [...string];
				var str = '';
				arrBox.map((res, index) => {
					if (index > 0) {
						str += '*';
					} else {
						str += res;
					}
				});
				return str;
			}
		}
		/*E 隐藏字符*/
	}
};
</script>

<style lang="scss">
.container {
	background-color: #f7f7f7;
	font-size: 24upx;
	.index_banner {
		width: 100%;
		height: 334upx;
		display: block;
	}
	.index_list {
		padding: 30upx;
		margin-top: 20upx;
		background-color: #ffffff;
	}
	.list_name {
		font-size: 32upx;
		color: #333333;
		font-weight: 500;
	}
	.list_num {
		font-size: 26upx;
		color: #646464;
	}
	.list_time {
		color: #959595;
		font-size: 24upx;
	}
	.list_follow {
		width: 28upx;
		height: 28upx;
	}
	.img {
		// border:2upx solid #000000;
		margin-bottom: 10upx;
		margin-right: 16upx;
	}
	.img:nth-child(3n) {
		margin-right: -2upx;
	}
	.uni-flex-list_img {
		display: flex;
		justify-content: flex-start;
		flex-wrap: wrap;
		align-items: center;
	}
	.imageError {
		background: #f5f7fa;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 14px;
		color: #c0c4cc;
		vertical-align: middle;
	}
	.noneDate {
		text-align: center;
	}
}
</style>
